import { BaseElement, createClassName, defineElement, html, property, query } from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { coderSearchStyles } from './coderSearchStyle';
import type { Button as ButtonT } from '@umetrics/sartorius-ui-button';
import type { TextField as TextFieldT } from '@umetrics/sartorius-ui-text-field';
import type { Checkbox as CheckboxT } from '@umetrics/sartorius-ui-checkbox';
import type { Icon as IconT } from '@umetrics/sartorius-ui-icon';
import {
    closeSearchPanel,
    findNext,
    findPrevious,
    replaceAll,
    replaceNext,
    SearchQuery,
    selectMatches,
    setSearchQuery,
} from '@codemirror/search';

@defineElement({
    tagName: 'coder-search',
    styles: coderSearchStyles,
    version,
})
export class CoderSearch extends BaseElement {
    // Static members
    private static Button: typeof ButtonT;
    private static TextField: typeof TextFieldT;
    private static Checkbox: typeof CheckboxT;
    private static Icon: typeof IconT;

    @property({ type: Object, reflect: false, attribute: false })
    view;
    @property({ type: Boolean, reflect: false, attribute: false })
    readonly;

    @query('[class*="-input-find"]')
    private _inputFind?: TextFieldT;
    @query('[class*="-input-replace"]')
    private _replaceFind?: TextFieldT;
    @query('[class*="-match-case"]')
    private _caseField?: CheckboxT;
    @query('[class*="-regexp"]')
    private _regexpField?: CheckboxT;
    @query('[class*="-by-word"]')
    private _wordField?: CheckboxT;

    private _query: SearchQuery = new SearchQuery({
        search: '',
        replace: '',
        caseSensitive: false,
        regexp: false,
        wholeWord: false,
    });

    constructor() {
        super();

        this._keydownHandler = this._keydownHandler.bind(this);
        this._findNextAdjusted = this._findNextAdjusted.bind(this);
        this._findPrevAdjusted = this._findPrevAdjusted.bind(this);
    }

    connectedCallback() {
        CoderSearch.dynSubRegister(
            [
                { module: () => import('@umetrics/sartorius-ui-button'), elem: ['Button'] },
                { module: () => import('@umetrics/sartorius-ui-text-field'), elem: ['TextField'] },
                { module: () => import('@umetrics/sartorius-ui-checkbox'), elem: ['Checkbox'] },
                { module: () => import('@umetrics/sartorius-ui-icon'), elem: ['Icon'] },
            ],
            () => {
                super.connectedCallback();
            }
        );
    }

    protected firstUpdated(_changed) {
        super.firstUpdated(_changed);

        this._inputFind?.updateComplete.then(() => {
            this._inputFind?.focus();
        });
    }

    protected update(params) {
        super.update(params);
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            readonly: !!this.readonly,
        });

        return html`
            <div class="${rootClasses}">
                <div class="${baseClassName}-cols" style="flex: 1;max-width: 550px;">
                    <${CoderSearch.TextField.tag} class="${baseClassName}-input-find" size="small" placeholder="Find"
                                                  value=${this._query.search}
                                                  @input=${() => this._commit()}
                                                  @keydown=${(e) => this._keydownHandler(e)}>
                    </${CoderSearch.TextField.tag}>
                    <${CoderSearch.TextField.tag} class="${baseClassName}-input-replace" size="small"
                                                  placeholder="Replace" value=${this._query.replace}
                                                  @input=${() => this._commit()}
                                                  @keydown=${(e) => this._keydownHandler(e)}>
                    </${CoderSearch.TextField.tag}>
                </div>

                <div class="${baseClassName}-cols">
                    <div class="${baseClassName}-row">

                        <${CoderSearch.Button.tag} class="${baseClassName}-select-next-button"
                                                   .design=${'inline-button'}
                                                   .type=${'tertiary'}
                                                   @click=${() => this._findNextAdjusted(this.view)}>
                            Next
                        </${CoderSearch.Button.tag}>
                        <${CoderSearch.Button.tag} class="${baseClassName}-select-previous-button"
                                                   .design=${'inline-button'}
                                                   .type=${'tertiary'}
                                                   @click=${() => this._findPrevAdjusted(this.view)}>
                            Previous
                        </${CoderSearch.Button.tag}>
                        <${CoderSearch.Button.tag} class="${baseClassName}-select-all-button" .design=${'inline-button'}
                                                   .type=${'tertiary'} @click=${() => selectMatches(this.view)}>
                            All
                        </${CoderSearch.Button.tag}>
                    </div>
                    <div class="${baseClassName}-row">
                        <${CoderSearch.Button.tag} class="${baseClassName}-replace-button" .design=${'inline-button'}
                                                   .type=${'tertiary'} @click=${() => replaceNext(this.view)}>
                            Replace
                        </${CoderSearch.Button.tag}>
                        <${CoderSearch.Button.tag} class="${baseClassName}-replace-all-button"
                                                   .design=${'inline-button'}
                                                   .type=${'tertiary'} @click=${() => replaceAll(this.view)}>
                            Replace all
                        </${CoderSearch.Button.tag}>
                    </div>
                </div>

                <div class="${baseClassName}-cols" style="align-self: flex-start;">
                    <div class="${baseClassName}-row">
                        <${CoderSearch.Checkbox.tag} class="${baseClassName}-match-case" .checkboxSize=${'small'}
                                                     ?checked=${this._query.caseSensitive}
                                                     @change=${() => this._commit()}>
                            Match case
                        </${CoderSearch.Checkbox.tag}>
                        <${CoderSearch.Checkbox.tag} class="${baseClassName}-regexp" .checkboxSize=${'small'}
                                                     ?checked=${this._query.regexp} @change=${() => this._commit()}>
                            Regexp
                        </${CoderSearch.Checkbox.tag}>
                        <${CoderSearch.Checkbox.tag} class="${baseClassName}-by-word" .checkboxSize=${'small'}
                                                     ?checked=${this._query.wholeWord} @change=${() => this._commit()}>
                            By word
                        </${CoderSearch.Checkbox.tag}>
                    </div>
                </div>
            </div>

            <${CoderSearch.Button.tag} class="${baseClassName}-close" .compact=${true} .buttonSize=${'extra-small'}
                                       type="secondary" @click=${() => closeSearchPanel(this.view)}>
                <${CoderSearch.Icon.tag} slot="icon-slot" icon="cancel">
                </${CoderSearch.Icon.tag}>
            </${CoderSearch.Button.tag}>
        `;
    }

    private _findNextAdjusted(view) {
        findNext(view);
        setTimeout(() => this._adjustSearchPanelPosition(view), 100);
    }

    private _findPrevAdjusted(view) {
        findPrevious(view);
        setTimeout(() => this._adjustSearchPanelPosition(view), 100);
    }

    private _adjustSearchPanelPosition(view) {
        const match = view.dom.querySelector('.cm-searchMatch-selected');
        if (!match) return;

        const panel = view.dom.querySelector('.cm-panels');
        if (!panel) return;

        const editorRect = view.dom.getBoundingClientRect();
        const matchRect = match.getBoundingClientRect();
        const distanceToTop = matchRect.top - editorRect.top;
        const distanceToBottom = editorRect.bottom - matchRect.bottom;
        const bottom = distanceToTop < distanceToBottom;

        panel.classList.add(bottom ? 'cm-panels-bottom' : 'cm-panels-top');
        panel.classList.add(bottom ? 'cm-panels-top' : 'cm-panels-bottom');
        panel.style.bottom = bottom ? '0' : `calc(100% - ${panel.clientHeight}px)`;
    }

    private _commit() {
        const searchQuery = new SearchQuery({
            search: this._inputFind!.value,
            caseSensitive: this._caseField!.checked,
            regexp: this._regexpField!.checked,
            wholeWord: this._wordField!.checked,
            replace: this._replaceFind!.value,
        });
        if (!searchQuery.eq(this._query)) {
            this._query = searchQuery;
            this.view.dispatch({ effects: setSearchQuery.of(searchQuery) });
        }
    }

    private _keydownHandler(e) {
        if (e.keyCode === 13) {
            e.preventDefault();
            if (e.target === this._inputFind) {
                (e.shiftKey ? this._findPrevAdjusted : this._findNextAdjusted)(this.view);
            } else if (e.target === this._replaceFind) {
                replaceNext(this.view);
            }
        } else if (e.keyCode === 27) {
            e.preventDefault();
            closeSearchPanel(this.view);
        }
    }
}
