import { css, cssSelector, GenerateStyles } from '@umetrics/sartorius-ui-base';

export const coderSearchStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: block;
                margin: 2px 16px 4px 6px;
                position: relative;
            }

            ${root} {
                display: flex;
                flex-direction: row;
                max-width: calc(100% - 32px);
                gap: 4px;
            }

            ${child('cols')} {
                display: flex;
                flex-direction: column;
            }

            ${child('row')} {
                display: flex;
                flex-direction: row;
                gap: 4px;
                align-items: center;
                flex: 1;
                flex-wrap: wrap;
            }

            ${child('input-find')} {
                display: block;
                --textfield-root-max-width: 550px;
                margin-top: 3px;
                flex: 1 1 100%;
            }

            ${child('input-replace')} {
                display: block;
                --textfield-root-max-width: 550px;
                margin-top: 3px;
                flex: 1 1 100%;
            }

            ${is('readonly')} ${child('input-replace')} {
                display: none;
            }

            ${is('readonly')} ${child('replace-button')} {
                display: none;
            }

            ${is('readonly')} ${child('replace-all-button')} {
                display: none;
            }

            ${child('close')} {
                position: absolute;
                top: 10px;
                right: 0;
            }
        `,
    ];
};
