import { css, cssSelector, GenerateStyles } from '@umetrics/sartorius-ui-base';

export const coderStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: block;
                width: 100%;
                height: 100%;
                max-height: inherit;
                max-width: inherit;
                text-align: initial;

                color: var(--editor-text-color, var(--color-primary-3));
            }

            ${root} {
                height: 100%;
                max-height: inherit;
                max-width: inherit;

                display: flex;
                flex-direction: column;
            }

            ${child('editor')} {
                border: 1px solid var(--color-secondary-4);

                flex: 1;
                flex-basis: 0;
                min-height: 0;

                position: relative;
                background-color: var(--editor-bg-color, var(--color-primary-2));
            }

            ${child('editor')}:focus-within:after {
                content: ' ';
                display: block;
                position: absolute;
                outline: 2px solid var(--color-secondary-10);
                z-index: 2;
                pointer-events: none;
                inset: 1px;
            }

            ${child('inner')} {
                position: relative;
                width: max-content;
                min-width: 100%;
                height: 100%;
            }

            ${child('stat')} {
                font-size: 12px;
                color: var(--color-secondary-8);

                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                margin-top: 7px;
            }

            ${child('label-wrapper')} {
                display: flex;

                justify-content: space-between;
                align-items: center;
            }

            ${child('label-icon')} {
                font-size: 20px;
                color: var(--color-secondary-8);
            }

            ${child('label')} {
                display: inline-block;
                text-align: left;
                font-size: 14px;
                color: var(--color-secondary-8);
                margin-bottom: 0px;
            }

            ${is('label')} ${child('label-wrapper')} {
                margin-bottom: 7px;
                height: 21px;
            }

            ${child('info-wrapper')} {
                display: flex;
                justify-content: space-between;
                width: 100%;
            }

            ${child('feedback-wrapper')} {
                position: relative;
                display: flex;
                justify-content: flex-start;
                white-space: normal;
                flex: 1;
            }

            ${child('feedback-message')} {
                margin-top: 4px;
            }

            ${child('editor')}:hover {
                border: 1px solid var(--color-secondary-8);
            }

            ${is('validation')} ${child('label')} {
                color: var(--color-traffic-light-1);
            }

            ${is('validation')} ${child('label-icon')} {
                color: var(--color-traffic-light-1);
            }

            ${is('validation')} ${child('editor')} {
                border: 1px solid var(--color-traffic-light-1);
            }

            ${is('validation')} ${child('editor')}:focus-within:after {
                outline: 2px solid var(--color-traffic-light-1);
            }

            ${is('disabled')} ${child('line')} {
                opacity: 0.3;
            }

            ${is('disabled')} ${child('highlight')} {
                opacity: 0.3;
            }

            ${is('readonly')} .cm-editor {
                cursor: default;
            }

            ${is('readonly')} .cm-editor .cm-cursor {
                caret-color: transparent !important;
                visibility: hidden;
            }

            .cm-editor .cm-scroller {
                overflow: visible !important;
            }

            .cm-gutters,
            .cm-content {
                line-height: var(--line-height, 14pt);
                font-family: var(--font-family, 'Roboto Mono'), 'Courier New', Courier, monospace;
                font-size: var(--font-size, 0.875rem);
            }

            .cm-focused .cm-cursor {
                display: block;
            }

            .cm-focused .cm-cursorLayer {
                animation: 1.2s steps(1) 0s infinite normal none running cm-blink;
            }
        `,
    ];
};
