export type Language = 'none' | 'default' | 'clike' | 'css' | 'js' | 'markup' | 'python' | 'json' | 'markdown';

export enum CoderSlot {
    TOOLBAR_SLOT = 'toolbar-slot',
    LABEL_SLOT = 'label-slot',
    LABEL_ICON_SLOT = 'label-icon-slot',
}

export interface CoderProperties {
    // baseInput -->
    value?: string;
    label?: string;
    disabled?: boolean;
    additionalFeedbackType?: string;
    additionalFeedbackMessage?: string;
    // <-- baseInput

    readonly?: boolean;
    lines?: boolean; // show line numbers
    code?: string;
    language?: Language;
    stats?: boolean;
    indent?: string | number; // Numeric value as indent or (deprecated) string as "  " for indent of 2.

    opening?: string[]; // deprecated, see closeBrackets instead
    closing?: string[]; // deprecated, see closeBrackets instead
    closeBrackets?: boolean;

    setCursor?: (pos: number) => void;
    setSelect?: (from: number, to: number) => void;

    /* Emitted events, for react users */
    onCodeUpdated?: any;
}
