import {
    BaseElement,
    booleanConverter,
    createClassName,
    createRef,
    defineElement,
    html,
    property,
    Ref,
    ref,
    render,
    state,
} from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { coderStyles } from './coderStyle';
import { CoderProperties, CoderSlot, Language } from './coderProperties';
import { EventDispatcherMixin } from '@umetrics/sartorius-ui-mixins';
import { FeedbackMessageType } from '@umetrics/sartorius-ui-feedback-message';

import { EditorView } from 'codemirror';
import { Compartment, EditorState } from '@codemirror/state';

import { vim } from '@replit/codemirror-vim';
import {
    crosshairCursor,
    drawSelection,
    dropCursor,
    highlightActiveLine,
    highlightActiveLineGutter,
    highlightSpecialChars,
    keymap,
    lineNumbers,
    rectangularSelection,
} from '@codemirror/view';
import {
    bracketMatching,
    defaultHighlightStyle,
    foldKeymap,
    indentOnInput,
    syntaxHighlighting,
} from '@codemirror/language';
import { javascript as langJs } from '@codemirror/lang-javascript';
import { css as langCss } from '@codemirror/lang-css';
import { html as langHtml } from '@codemirror/lang-html';
import { python as langPython } from '@codemirror/lang-python';
import { json as langJson } from '@codemirror/lang-json';
import { defaultKeymap, history, historyKeymap, indentWithTab } from '@codemirror/commands';
import { autocompletion, closeBrackets, closeBracketsKeymap, completionKeymap } from '@codemirror/autocomplete';
import { highlightSelectionMatches, search, searchKeymap } from '@codemirror/search';
import { CoderEvent } from './coderEvent';
import { CoderSearch } from './coderSearch';

@defineElement({
    tagName: 'coder',
    styles: coderStyles,
    version,
})
export class Coder extends EventDispatcherMixin(BaseElement) implements CoderProperties {
    // Static members
    private static Scrollbar;
    private static FeedbackMessage;

    // Private members

    // Protected members

    // Public members

    // baseInput -->
    @property({ type: String, reflect: false })
    label;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    disabled = false;

    @property({ type: String, attribute: 'feedback-type' })
    additionalFeedbackType = 'basic';
    @property({ type: String, attribute: 'feedback-mess' })
    additionalFeedbackMessage = '';

    @property({ type: String, reflect: false })
    get value() {
        return this._code;
    }

    set value(code) {
        this.code = code;
    }

    // <-- baseInput

    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    readonly;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    lines = false;
    @property({ type: String, reflect: true })
    language = 'clike' as Language;

    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    stats = false;
    @property({ type: String, reflect: false })
    indent?: string | number;

    @property({ type: String, reflect: false })
    get code() {
        return this._code;
    }

    set code(code) {
        const prev = this._code;

        if (this._code !== code) {
            // Brute replace \r\n's with \n's. Note that \n's are dispatched in event later.
            this._code = code ? code.replace(/\r\n/g, '\n') : '';
            this._updateEditorDoc(this._editor, this._code);

            this.requestUpdate('code', prev);

            // Inform client that code was changed by prop. This seems weird if client both set prop and catch event but
            // in case when prop is set by something else then it makes sense to sync client.
            this.dispatch(CoderEvent.CodeUpdated, this._eventDetails());
        }
    }

    @property({ type: Boolean, reflect: true, attribute: 'close-brackets', converter: booleanConverter })
    closeBrackets;

    @state()
    protected _focus = false;
    protected _editor?: EditorView;

    @state()
    private _code = '';

    @state()
    private _cursorPos: { s: number; e: number; sCol: number; sRow: number; eCol: number; eRow: number } = {
        s: 0,
        e: 0,
        sCol: 0,
        sRow: 0,
        eCol: 0,
        eRow: 0,
    };
    @state()
    private _labelSlotted;
    @state()
    private _labelIconSlotted;

    private _container: Ref<HTMLElement> = createRef();

    private _vimCompartment: Compartment = new Compartment();
    private _languageCompartment = new Compartment();
    private _readonlyCompartment = new Compartment();
    private _linesCompartment = new Compartment();
    private _closeBracketsCompartment = new Compartment();
    private _indentationCompartment = new Compartment();

    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = [];
        imports.push({ module: () => import('@umetrics/sartorius-ui-scrollbar'), elem: ['Scrollbar'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-feedback-message'), elem: 'FeedbackMessage' });

        Coder.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
        });
        Coder.subRegister(CoderSearch);
    }

    focus(_?: FocusOptions) {
        this._editor?.focus();
        this._viewUpdate({
            focusChanged: true,
            view: this._editor,
        });
    }

    blur() {
        const cm = this.shadowRoot!.querySelector('[class*="cm-content"]') as HTMLElement;
        cm.blur();
        this._viewUpdate({
            focusChanged: true,
            view: this._editor,
        });
    }

    public getSelectionStart() {
        return this._editor?.state.selection.main.anchor;
    }

    public getSelectionEnd() {
        return this._editor?.state.selection.main.head;
    }

    public setCursor(pos, end = pos) {
        this.setSelect(pos, end);
    }

    public setSelect(from, to) {
        if (!this._editor) return;

        const selection = { anchor: from, head: to };

        // Create an effect to update the selection range
        this._updateEditor(this._editor, { selection });
    }

    protected firstUpdated(_changed) {
        super.firstUpdated(_changed);

        /* Do some codemirror styling here */
        const fullHeightTheme = EditorView.theme({
            '&': { overflow: 'hidden', height: '100%', outline: 0, width: '100%', minWidth: '100%' }, // Make the editor take the full height
            '.cm-scroller': { height: '100%' }, // Make the scrolling area take full height too
            '&.cm-focused': { outline: 0, border: 0 }, // Have own focus effect on the inner::after
            '.cm-gutters': {
                color: 'var(--lines-text-color, var(--color-primary-3))',
                backgroundColor: 'var(--lines-bg-color, var(--color-secondary-2))',
            },
            '.cm-lineNumbers': { minWidth: '40px' },
            '.cm-panels': {
                position: 'absolute',
                transition: '0.2s bottom 0s ease-in-out',
                zIndex: 1,
                paddingBottom: '9px', // Make room for scroll
            },
            '.cm-cursor': { borderLeftWidth: '2px' }, // Some wider cursor
        });

        const customKeymap = keymap.of([
            {
                key: 'Mod-Alt-M',
                run: (editorView) => {
                    this._toggleVimMode(editorView);
                    return true;
                },
            },
            indentWithTab,
        ]);

        const customSearchExtension = search({
            createPanel: (view: EditorView) => {
                const dom = document.createElement('div');
                dom.className = 'custom-search-panel';
                const searchTemplate = html`
                    <${CoderSearch.tag} .view=${view} .readonly=${this.readonly}></${CoderSearch.tag}>`;
                render(searchTemplate, dom);

                return {
                    dom,
                };
            }, // Override only the UI, keep functionality
        });

        this._editor = new EditorView({
            doc: this.code,
            extensions: [
                this._vimCompartment.of([]),
                //esLint(),
                //lintGutter(),

                this._linesCompartment.of(this.lines ? lineNumbers() : []),

                // Basic setup...
                highlightActiveLineGutter(),
                highlightSpecialChars(),
                history(),
                //foldGutter(),
                drawSelection(),
                dropCursor(),
                EditorState.allowMultipleSelections.of(true),
                indentOnInput(),
                bracketMatching(),

                autocompletion(),
                rectangularSelection(),
                crosshairCursor(),
                highlightActiveLine(),
                highlightSelectionMatches(),
                keymap.of([
                    ...closeBracketsKeymap,
                    ...defaultKeymap,
                    ...searchKeymap,
                    ...historyKeymap,
                    ...foldKeymap,
                    ...completionKeymap,
                    //...lintKeymap,
                ]),
                // ...Basic setup

                this._languageCompartment.of(this._getLanguageExtension(this.language)),

                this._readonlyCompartment.of(EditorState.readOnly.of(!!this.readonly)),
                this._closeBracketsCompartment.of(this.closeBrackets ? closeBrackets() : []),
                syntaxHighlighting(defaultHighlightStyle, { fallback: true }),
                customKeymap,
                this._indentationCompartment.of(EditorState.tabSize.of(this._indentation(this.indent))),
                fullHeightTheme,
                EditorView.updateListener.of((e) => this._viewUpdate(e)),
                customSearchExtension,
            ],
            parent: this._container.value!,
        });

        const editor = this.shadowRoot!.querySelector('.cm-scroller');
        const template = html`
            <${Coder.Scrollbar.tag} class="scrollbar" data-testid="coder-scrollbar"></${Coder.Scrollbar.tag}>`;
        render(template, editor!.parentElement!);
        const scrollbar = this.shadowRoot!.querySelector('.scrollbar');
        scrollbar!.appendChild(editor!);

        if (this._code) {
            this.dispatch(CoderEvent.CodeUpdated, this._eventDetails());
        }
    }

    protected update(params) {
        super.update(params);

        const paramUpdates: Record<string, (editor: EditorView | undefined, value: any) => void> = {
            code: (editor, value) => this._updateEditorDoc(editor, value),
            language: (editor, value) => this._updateEditorLanguage(editor, value),
            readonly: (editor, value) => this._updateEditorReadonly(editor, value),
            lines: (editor, value) => this._updateEditorLines(editor, value),
            closeBrackets: (editor, value) => this._updateEditorCloseBrackets(editor, value),
            indent: (editor, value) => this._updateEditorIndentation(editor, value),
        };

        for (const [key, updateFn] of Object.entries(paramUpdates)) {
            if (params.has(key) && this[key] !== params.get(key)) {
                updateFn(this._editor, this[key]);
            }
        }
    }

    protected _renderBeforeScrollArea(_) {
        return html``;
    }

    protected render() {
        const withLabel = this.label || this._labelSlotted || this._labelIconSlotted;
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            readonly: this.readonly,
            disabled: this.disabled,
            lines: this.lines,
            label: withLabel,
            validation: this.additionalFeedbackType === FeedbackMessageType.Failure,
        });

        const endStat =
            this._focus && this._cursorPos.eRow > -1
                ? html`<span class="${baseClassName}-end-stat">
                      - ${this._cursorPos.eRow + 1}:${this._cursorPos.eCol + 1}
                      (${this._cursorPos.e - this._cursorPos.s} chars)
                  </span>`
                : null;
        const startStat =
            this._focus && this._cursorPos.sRow > -1
                ? html`<span class="${baseClassName}-start-stat">
                      - ${this._cursorPos.sRow + 1}:${this._cursorPos.sCol + 1}</span
                  >`
                : null;

        const lang = this.language !== 'none' ? ` ${this.language}` : '';
        const bytes = this._code ? this._code.length : 0;
        const statsHtml = this.stats
            ? html` <div class="${baseClassName}-stat">${lang} ${bytes} chars ${startStat} ${endStat}</div> `
            : null;

        const labelHtml = html` <div class="${baseClassName}-label-wrapper">
            ${this._renderLabel(baseClassName)}
            <slot
                name="${CoderSlot.LABEL_ICON_SLOT}"
                class="${baseClassName}-label-icon"
                @slotchange="${(e) => this._labelIconSlotChange(e)}"
            ></slot>
        </div>`;
        /*
                <${Coder.Scrollbar.tag} class="${baseClassName}-scrollbar" style="--scroll-padding: ${
                    this.lines ? '4px 4px 4px 48px' : '4px 4px 4px 8px'
                }" data-testid="coder-scrollbar" ${ref(this._container)}>
                    <slot name="${CoderSlot.TOOLBAR_SLOT}"></slot>
                </${Coder.Scrollbar.tag}>
* */
        return html`
            <div class="${rootClasses}" ?default=${!this.hasAttribute('mycolors')}>
                ${labelHtml} ${this._renderBeforeScrollArea(baseClassName)}
                <div class="${baseClassName}-editor" ${ref(this._container)}>
                    <slot name="${CoderSlot.TOOLBAR_SLOT}"></slot>
                </div>
                <div class="${baseClassName}-info-wrapper">
                    <div class="${baseClassName}-feedback-wrapper">${this._renderFeedback(baseClassName)}</div>
                    ${statsHtml}
                </div>
            </div>
        `;
    }

    protected _renderLabel(baseClassName) {
        return this.label
            ? html`<label class="${baseClassName}-label" for="ta" title=${this.label}>${this.label}</label>`
            : html`<label class="${baseClassName}-label" for="ta">
                  <slot name="${CoderSlot.LABEL_SLOT}" @slotchange="${(e) => this._labelSlotChange(e)}"></slot>
              </label>`;
    }

    protected _renderFeedback(baseClassName, validationText = '', validationType = 'failure') {
        return html`
            <${Coder.FeedbackMessage.tag}
                message-type="${validationText ? validationType : this.additionalFeedbackType}"
                class="${baseClassName}-feedback-message">
                ${validationText || this.additionalFeedbackMessage}
            </${Coder.FeedbackMessage.tag}>
        `;
    }

    protected _eventDetails() {
        return { detail: this._code };
    }

    protected _updateEditor = (editor, effects) => editor?.dispatch(effects);

    private _indentation = (indent?: string | number): number => {
        let val;
        if (typeof indent === 'number') {
            val = indent;
        } else {
            val = (indent as string)?.length;
        }
        return val >= 0 ? val : 2;
    };

    private _viewUpdate(viewUpdate) {
        if (viewUpdate.focusChanged) {
            this._focus = viewUpdate.view.hasFocus;
        }
        if (viewUpdate.docChanged) {
            const code = viewUpdate.state.doc.toString();
            if (this._code !== code) {
                // This run when code was altered in editor box and not if setting code externally (eg by prop)
                this._code = code;
                this.dispatch(CoderEvent.CodeUpdated, this._eventDetails());

                // Make sure that the scrollbar component gets notified. This is needed
                // because scrollbar only watch resize of .cm-scroller,
                // which is height:100% and never resizes.
                // Now we emit resize event on every keystroke which might be a bit raw.
                // Best way would be to detect when row are added or deleted above height:100% and when
                // character are added or deleted above width:100%.
                this.dispatchEvent(new CustomEvent('resize', { composed: true, bubbles: true }));
            }
        }
        const viewState = viewUpdate.state;
        if (this._focus && viewState) {
            const anchor = viewState.selection.main.anchor;
            const head = viewState.selection.main.head;

            // Get line and column positions for the start (anchor)
            const startLine = viewState.doc.lineAt(anchor);
            const sRow = startLine.number - 1; // Line number (start row)
            const sCol = anchor - startLine.from; // Column number (start column)

            // Get line and column positions for the end (head)
            const endLine = viewState.doc.lineAt(head);
            const eRow = anchor !== head ? endLine.number - 1 : -1; // Line number (end row)
            const eCol = anchor !== head ? head - endLine.from : -1; // Column number (end column)

            // Map the positions to the format for _cursorPos
            this._cursorPos = {
                s: anchor, // Start position (index)
                e: head, // End position (index)
                sCol, // Start column
                sRow, // Start row
                eCol, // End column
                eRow, // End row
            };
        }
    }

    private _labelSlotChange = (e) => (this._labelSlotted = this._slotChange(e.target));
    private _labelIconSlotChange = (e) => (this._labelIconSlotted = this._slotChange(e.target));

    private _slotChange = (target: HTMLSlotElement) =>
        target.assignedNodes().filter((node) => node.nodeType === 1).length > 0;

    private _toggleVimMode(editor: EditorView) {
        const ext = this._vimCompartment.get(editor.state);
        const viewState = Array.isArray(ext) && ext.length > 0 ? [] : vim();
        this._updateEditor(editor, {
            effects: this._vimCompartment.reconfigure(viewState),
        });
    }

    private _getLanguageExtension = (lang: Language | string) => {
        switch (lang) {
            case 'js':
                return langJs();
            case 'css':
                return langCss();
            case 'markup':
                return langHtml({ selfClosingTags: true, matchClosingTags: true });

            case 'python':
                return langPython();
            case 'json':
                return langJson();
            case 'none':
                return []; // No syntax highlighting
            default:
                return langJs();
        }
    };

    private _updateEditorDoc = (editor, doc) =>
        this._updateEditor(editor, { changes: { from: 0, to: editor?.state.doc.length, insert: doc } });

    private _updateEditorLanguage = (editor, language) =>
        this._updateEditor(editor, {
            effects: this._languageCompartment.reconfigure(this._getLanguageExtension(language)),
        });

    private _updateEditorReadonly = (editor, readonly) =>
        this._updateEditor(editor, {
            effects: this._readonlyCompartment.reconfigure(EditorState.readOnly.of(readonly)),
        });
    private _updateEditorLines = (editor, lines) =>
        this._updateEditor(editor, {
            effects: this._linesCompartment.reconfigure(lines ? lineNumbers() : []),
        });
    private _updateEditorCloseBrackets = (editor, value) =>
        this._updateEditor(editor, {
            effects: this._closeBracketsCompartment.reconfigure(value ? closeBrackets() : []),
        });
    private _updateEditorIndentation = (editor, value) => {
        this._updateEditor(editor, {
            effects: this._indentationCompartment.reconfigure(EditorState.tabSize.of(this._indentation(value))),
        });
    };
}
